.projects_container {
  width: 80%;
  margin: 0 auto;
  background-color: transparent;
  
  
}
*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}


.project {
  width: 100%;
  /* height: 70vh; */
  margin: 6% 0% 6% 0%;
  box-shadow: var(--shadow);
  display: flex;
  border-radius: 20px;
  transition: all 0.5s ease 0s;
  box-shadow: rgba(99, 99, 99, 0.2) 0px 2px 8px 0px;
  
}

.project:hover {
  transform: scale(1.04);
  text-shadow: #FC0 1px 0 10px;
  overflow: hidden;
  box-shadow: rgba(0, 0, 0, 0.36) 0px 10px 15px 0px,
    rgba(0, 0, 0, 0.06) 0px 0px 0px 1px;
}
.project_videocontainer {
  flex-basis: 65%;
  border-radius: 20px;
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: 20px;
}

.project_videocontainer > div {
  width: 90%;
  height: 90%;

  border-radius: 20px;
}
.project_videocontainer > div > img {
  width: 100%;
  height: 100%;
  border: 3px solid var(--clr-primary);
  border-radius: 20px;
}

.project_information {
  flex-basis: 35%;
  border-radius: 30px;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 0% 3% 4% 3%;
  overflow-y: scroll;
  overflow-x: hidden;
  margin-bottom: 10px;
}
.project_information > h2 {
  font-size: larger;
  font-weight: bolder;
  margin-top: 1em;
  color: var(--clr-primary);
}
.project_information > p {
  margin-top: 1em;
  text-align: center;
}
.project_information > div:nth-child(3) {
  margin-top: 0.5em;
  display: flex;
  justify-content: space-evenly;
}

.project_information > div:nth-child(3) > svg {
  font-size: 3em;
  margin: 2% 3% 2% 3%;
  color: var(--clr-primary);
  transition: all 0.2s ease-in-out;
}

.project_information > div:nth-child(3) > svg:hover {
  font-size: 3.3em;
}

.project_information > div:nth-child(4) {
  margin-top: 0.6em;
  display: flex;
 
}
.onbt {
  margin: 0 1vw 0 1vw;
}
.btns {
  color: var(--clr-primary);
  border: 2px solid var(--clr-primary);
  border-radius: 5px;
  height: auto;
  cursor: pointer;
  padding: 10px;
  margin-top: 10px;
  box-shadow: rgba(191, 153, 221, 0.919) 0px 0px 0px 3px;
}
.btns:hover {
  content: "link";
  transform: scale(1.2);
  text-shadow: #FC0 1px 0 10px;
}

.project_information > h2 {
  margin-top: 1.6em;
  color: var(--clr-primary);
}
.project_information > h2:hover {
  text-shadow: #FC0 1px 0 10px;
  transform: scale(1.2);
}
.project_information::-webkit-scrollbar {
  display: none;
}

.allProjects {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
}
.project {
  border: 2px solid var(--clr-primary);
  flex-direction: column;
}
.section__title_different {
  box-shadow: rgba(6, 24, 44, 0.4) 0px 0px 0px 2px,
    rgba(6, 24, 44, 0.65) 0px 4px 6px -1px,
    rgba(255, 255, 255, 0.08) 0px 1px 0px inset;
    padding: 20px;
}


@media (max-width: 800px) {
  .project {
    flex-direction: column;
    /* height: 100vh; */
  }
  .project_videocontainer {
    flex-basis: 40%;
  }
  .project_information {
    flex-basis: 60%;
  }
  .allProjects {
    display: grid;
    grid-template-columns: repeat(1, 1fr);
  }
  .projects_container {
    width: 100%;
    margin: 0 auto;
    
  }
}
@media(max-width:350px){
  .Project{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
  }
  .projects_container{
    margin: 0;
    padding: 0;
    width: 100%;
  }
}
  
